body {
  margin: 0;
  font-family: 'Helvetica Neue', sans-serif;
  background-color: #fff;
  color: #000;
}

.navbar {
  padding: 1rem 2rem;
  display: flex;
  justify-content: flex-end;
}

.navbar ul {
  display: flex;
  list-style: none;
  gap: 2rem;
  margin: 0;
  padding: 0;
}

.navbar li {
  cursor: pointer;
  font-weight: 500;
}

.hero {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 4rem 6rem;
  flex-wrap: wrap;
}

.text-section {
  max-width: 600px;
}

.name {
  font-size: 4rem;
  font-family: 'Playfair Display', serif;
  line-height: 1.1;
  margin: 0;
}

.subtitle {
  font-size: 1.1rem;
  margin-top: 1rem;
}

.image-section img {
  max-width: 350px;
  width: 100%;
  height: auto;
}
